//------------------------------------------------------
// Ion Components Overridden
//------------------------------------------------------

ion-toolbar {
  --ion-background-color: #eee;
  --background: #eee;
  border: none;
  --border-width: 0px !important;
  height: 48px;  // Sets height of header menu buttons
}

ion-title.title-default {
  padding: 0px;
  font-size: 18px;
  font-weight: normal;
  text-transform: uppercase;
}

ion-content {
  --ion-background-color: #fff;
  --background: #fff;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  outline: none;

  .header {
    color: #222;
  }
  .message-list {
    width: 100%;
  }
}

ion-footer {
  .outer-content {
    --ion-background-color: #eee;
    --background: #eee;
  }
}

ion-tab-bar {
  border: unset;
  margin: 4px;
  height: 48px;  // Sets height of footer page buttons
}

ion-tab-button {
  font-size: 12px;

  ion-label {
    text-transform: uppercase;
  }
}

ion-row {
  margin: 8px auto;

  ion-col {
    font-weight: normal;
    font-size: 14px;
  }
}

ion-button  {
  outline: none;
  --border-radius: 4px;
}

button {
  border-radius: 4px !important;  // overwrites internal button styles
  outline: none;
}

.debug-info {
  float: right;
  color: gray;
  font-size: 12px;
  margin-right: 10px;
}

//------------------------------------------------------
// Schedule Page Styles
//------------------------------------------------------
#schedule-page {
  .center {
    margin-left: auto;
    margin-right: auto;
    display: block;
  }

  .filter-not-applied {
    display: none;
  }

  .filter-applied {
    font-size: 84px;
    color: red;
    position: absolute;
    top: -30px;
    right: 3px;
  }

  // Add Daily Note Button next to week header event list
  .add-daily-note-button {
    color: #fff;
    background-color: #424242;
    font-size: 12px;
    padding: 4px;
    vertical-align: text-bottom;
    float: right;
    outline: none;
  }
}

//---------------------------------------------------
// Schedule | Message: Filter Modal
//----------------------------------------------------
.outer-content {
  .shadow-event-label {
    margin-left: 10px;
  }
}

.apply-button {
  font-size: 16px;
  --background: var(--ion-color-secondary);
  margin: 6px;
}

//---------------------------------------------------
// Week Picker Control - Schedule Page
//----------------------------------------------------
.chooser {
  background: var(--ion-item-background, var(--ion-background-color, #fff));

  .top {
    margin: 6px 0px;
    display: flex;
    align-items: center;
    width: 100%;

    .arrow {
      text-align: center;
      font-size: 25px;
      background-color: #eee;
      outline: none;
    }

    .month-year-container {
      text-align: center;
      flex: 1;
      text-transform: uppercase;

      .datetime-picker-container {
        width: 100%;
        display: block;

        .datetime-picker {
          display: inline-block;
          vertical-align: middle;
        }
      }
    }
  }

  .weekday-list {
    display: flex;
    width: 100%;
    font-size: 14px;
    margin: 4px auto;

    .weekday-button {
      flex: 1;
      text-align: center;
      padding: 0 10px 0 5px;
      background-color: #eee;
      line-height: 1.4;

      .today {
        border-bottom: 2px solid #ddd;
      }

      .not-today {
        border-bottom: 2px solid #eee;
      }
    }
  }
}

//---------------------------------------------------
// CARDS
//---------------------------------------------------

.card {
  margin: 2px auto;
  padding: 8px;
  font-family: Helvetica, Arial, sans-serif;
  display: flex;
  align-items: center;
  background-color: #fff;
  line-height: 1.4;
  width: 100%;

  .card-first-col {
    min-width: 30px;
    font-size: 14px;
    display: flex;
    align-items: stretch;
    flex-direction: column;
  }

  .card-second-col {
    font-size: 14px;
    line-height: 1.4;
    margin-left: 10px;
    text-align: left;
    padding: 0px 10px;
    white-space: pre-line;

    .dept-position-row {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    .note-text-row-oneline {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    .break-div {
      color: #bbbbbb;
      margin: 3px 0 3px 0;
      width: 100%;
    }
  }
}

//--------------------------------------------------
// Daily Notes
//---------------------------------------------------

.daily-note {
  .card-second-col {
    border-left: 7px solid #424242;
  }
}

.IonTitle--DailyNote {
  background: #eee;
}

// Edit Daily Note Modal
.edit-daily-modal-content {
  ion-textarea {
    padding: 5px;
    display: block;
  }

  .character-counter {
    float: right;
    margin-right: 15px;
  }
}

//--------------------------------------------------
// Events
//--------------------------------------------------

.header {
  color: #222;
  padding: 20px 10px 12px;
  font-size: 16px;
  border-bottom: 1px solid #ddd;
}

//---------------------------------------------------
// Edit Event Modal
//---------------------------------------------------

.outer-content {
  font-size: 14px;

  ion-row {
    align-items: center;

    ion-col {
      .label {
        color: #aaa;
      }
      P .select {
        border: 1px solid #ccc;
        padding: 5px;
        margin: 0;
      }

      .textarea {
        border: 1px solid #ccc;
        padding-left: 10px;
      }

      .input-textbox {
        border: 1px solid #ccc;
        height: 32px;
      }
    }
  }
}

.invisible {
  display: none;
}

.footer {
  display: flex;

  .apply-button {
    width: 100%;
    font-size: 16px;
    font-weight: 1;
    margin: 6px;
  }

  .delete-button {
    width: 100%;
    font-size: 16px;
    font-weight: 1;
    --background: #d9534f;
    margin: 6px;
  }
}

.request-button {
  margin: 20px 15px 0 10px;
  font-size: 16px;
}

// Recurrence Update Type Selector
//---------------------------------------------------
.recurrence-update-modal .modal-wrapper {
  top: 45%;
  width: 75%;
  height: 325px;
  position: absolute;
  display: block;

  ion-row {
    height: 60px;

    ion-col {
      .apply-button {
        width: 100%;
        font-size: 20px;
        margin: 5px 0;
      }

      ion-button.cancel-btn {
        width: 100%;
        font-size: 20px;
        margin: 5px 0;
      }
    }
  }
}

// Weekday List
//---------------------------------------------------
.weeklist-container {
  width: 100%;

  .day {
    .day-checkbox {
      margin-right: 2px;
    }

    .day-abbr {
      margin-right: 10px;
    }
  }
}

