//---------------------------------------------------
// MODAL PAGES
//---------------------------------------------------

.label {
  font-size: 14px;
  font-weight: normal;
  margin: 14px 0px 10px;
  color: #222 !important;
}

.checkbox {
  padding: 0;
  margin: 5px 0 5px 10px;
  transform: scale(1.5);
}

.select {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px;
  margin: 0;
}

.input {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 5px;
  margin: 0;
}

