  border: 1px solid #ccc;
  border-radius: 4px;
}

.error-message {
  height: 26px;
  color: red;
  font-weight: bold;
  font-size: 12px;
}

.usage-message {
  margin: 20px 10px 10px 10px;
  font-size: 24px;
}

button:disabled > div > div.alert-radio-icon {
  visibility: hidden;
}

.clockout-button {
  font-weight: 1;
  --background: #931c21;
}

.cancel-button {
  font-weight: 1;
}

//---------------------------------------------------
// CLOCK PAGE
//---------------------------------------------------

.label-list-header {
  font-size: 16px;
  font-weight: normal;
  margin: 14px 0px 10px;
  color: #222 !important;
}

.item {
  --background: #fff;
  margin: 0px 2px;
  
}

.item:last-child {
  // --border-style: none;
}

// Removes line on bottom of Item
//--------------------------------
.item-no-border {
  --border-style: none;
}

#clock-page {
  height: 100%;

  ion-list-header {
    font-weight: normal;
  }

  .scroll {
    background: #ededed;
  }

  .outer-content {
    .grid {
      .current-event-row {
        .current-event-col {
          display: inherit;
        }
      }
    }
  }

  .todaylist {
    margin-bottom: 110px;
    background: #fff;
  }

  .center {
    margin-left: auto;
    margin-right: auto;
    display: block;
  }
}

//---------------------------------------------------
// CLOCK: PAY PERIOD TAB
//---------------------------------------------------
.list-header {
  border-bottom: 1px solid #ddd;
}

.list-footer {
  border-top: 1px solid #ddd;
}

.payperiod-list-buttons {
  font-size: 14px;
  background-color: #fff;
  border: none;
  outline: none;
  padding: 0px;
}

.detail-modal .modal-wrapper {
  top: 25%;
  width: 65%;
  height: 45%;
  position: absolute;
  display: block;
}

//---------------------------------------------------
// CLOCK FOOTER PANEL
//---------------------------------------------------

.clock-controls {
  width: 100%;
  margin: 6px auto;

  .switch-button-container {
    .switch-button {
      font-weight: 1;
      display: flex;
    }
  }

  .clockin-button-container {
    .clockin-button {
      font-weight: 1;
      --background: green;
      display: flex;
    }
  }

  .clockout-button-container {
    font-size: 16px;
    align-content: space-between;
    display: flex;

    .clockout-button {
      width: 100%;
      font-weight: 1;
      --background: #931c21;
    }

    .break-button {
      width: 100%;
      font-weight: 1;
      --background: #daa520;
    }
  }
}

.clockout-modal .modal-wrapper {
  top: 35%;
  width: 50%;
  height: 33%;
  position: absolute;
  display: block;
}

.break-selector-modal .modal-wrapper {
  top: 20%;
  width: 65%;
  height: 50%;
  position: absolute;
  display: block;

  .cancel-button {
    margin: 6px;
  }
}

//---------------------------------------------------
// CLOCK FOOTER: Map
//---------------------------------------------------

.map-button {
  display: block;
}

#show-map-button {
  position: absolute;
  right: 0;
  padding-right: 15px;
}

#map-container {
  height: 150px;
  border: 1px solid #ddd;
  margin: 2px;

  .map-canvas {
    height: 100%;
    width: 100%;
    background-color: orange;
    opacity: 0;
    transition: opacity 250ms ease-in;
  }

  .show-map {
    opacity: 1;
  }
}

//---------------------------------------------------
// MESSAGE: View Message Modal
//---------------------------------------------------

.request-button {
  margin: 20px 15px 0 10px;
  font-size: 20px;
  height: 50px;
  --background: var(--ion-color-secondary);
}

.outer-contentMessage {
  .messageIonRow-header {
    padding: 2px 10px;
    display: block;
    font-size: 14px;
    color: #999;

    .messageHeaderSubject {
      font-size: 18px;
      color: #000;
      white-space: nowrap;
      display: inline-block;
      margin: 0px;
      padding: 4px 2px;
    }

    .messageHeaderDetails {
      display: inline-block;
      margin: 0px;
      padding: 2px;
    }

    .readCounter {
      text-align: right;
      float: right;
    }
  }

  .messageIonRow-content {
    line-height: 1.4;
    margin: 8px;
    display: block;

    .messageBlock {
      display: inline-block;
      padding-top: 14px;
      font-size: 15px;
      border-top: 1px solid #ebebeb;
    }
  }
}

//---------------------------------------------------
// Message Item
//---------------------------------------------------

.message {
  width: 100%;
  padding: 14px 18px;
  background-color: white;
  line-height: 1.4;
  text-align: left;

  .top-line {
    font-size: 15px;
    display: flex;
    justify-content: space-between;

    .subject {
      float: left;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .time {
      float: right;
      padding: 0px 4px;
    }
  }

  .bottom-line {
    width: inherit;
    font-size: 14px;
    color: gray;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

//---------------------------------------------------
// Time Overview Display
//---------------------------------------------------
.segment-content {
  margin: 0 15px 0 15px;
}

//---------------------------------------------------
// No Events to Show
//---------------------------------------------------
div.no-events-wrapper {
  margin: 35px 0 0 35px;
  .no-events-span {
    font-size: 24px;
    color: gray;
  }
}

//---------------------------------------------------
// Option Selector
//---------------------------------------------------
.option-selector {
  position: relative;
  float: right;
  .option {
    height: 40px;
    border: 1px solid darkgrey;
    border-radius: 5px;
    padding: 0 25px;
    margin-left: 5px;
  }

  .selected-option {
    background-color: darkgrey;
  }
}

//---------------------------------------------------
// HOOKS: Use Orbital Dialog Box
//---------------------------------------------------
.orbital-dialog-modal .modal-wrapper {
  height: 30%;
  width: 80%;
  top: 50%;
  padding: 10%;
  position: absolute;
  display: block;

  ion-row {
    height: 60px;

    ion-col {
      .apply-button {
        width: 100%;
        font-size: 20px;
        margin: 5px 0;
      }

      ion-button.cancel-btn {
        width: 100%;
        font-size: 20px;
        margin: 5px 0;
      }
    }
  }
}

//---------------------------------------------------
// PAGES
//---------------------------------------------------

// Main Tabs
//---------------------------------------------------
.hasMessages {
  font-size: 10px;
  color: white;
  background-color: red;
  position: fixed;
  margin-top: -10px;
  margin-left: 25px;
  border-radius: 100%;
  padding: 2px 6px;
  z-index: 100;
}

.noMessages {
  visibility: hidden;
}

// Logout
//---------------------------------------------------
.logout-content {
  --background: #cc5600;

  .login-logo img {
    margin: 15px 0 0 15px;
    max-width: 90%;
    filter: brightness(0) invert(1);
  }

  .label {
    color: #eee;
    font-size: 24px;
    margin-top: 30px;
    margin-left: 15px;
  }
}

// Login
//---------------------------------------------------
.login-content {
  --background: #cc5600;

  .login-logo img {
    margin: 15px 0 0 15px;
    max-width: 90%;
    filter: brightness(0) invert(1);
  }

  .form-list {
    background-color: #cc5600;

    .item {
      background-color: #cc5600;

      .label {
        color: #eee;
        font-size: 24px;
        margin-left: 15px;
      }

      .field-container {
        margin: 5px 25px 15px 15px;

        .input {
          --background: #eee;
          height: 50px;
          font-size: 26px;
          border-radius: 4px;
          padding-left: 5px;
          width: 100%;
        }
      }
    }
  }

  .login-button {
    margin: 5px 15px 0 10px;
    font-size: 24px;
    height: 55px;
    --background: var(--ion-color-tertiary);
  }
}

// Dashboard Page
//---------------------------------------------------
#dashboard {
  ion-list-header {
    color: #bbbbbb;
    font-weight: 500;
  }

  ion-content {
    --background: #ffffff;

    .center {
      margin-left: auto;
      margin-right: auto;
      display: block;
    }
  }
}

// Account Page
//---------------------------------------------------
#account-page {
  img {
    max-width: 140px;
    border-radius: 50%;
  }
}
